<template>
  <div class="main">
      <h2>注意 添加图书的时候默认使用mockjs生成了对应的图片</h2>
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
            <el-form-item label="请输入图书名称" prop="pass">
                <el-input type="text" v-model="ruleForm.pass" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="请输入图书简介" prop="area">
                <el-input type="textarea" v-model="ruleForm.area" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
  </div>
</template>

<script>
import { mapMutations } from "vuex"
import mockjs from "mockjs";
var mock = mockjs.Random;
export default {
    data() {
      var checkAge = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('图书名称不能为空!'));
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error('请输入数字值'));
          } else {
            if (value < 18) {
              callback(new Error('必须年满18岁'));
            } else {
              callback();
            }
          }
        }, 1000);
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('图书名称不能为空'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validateArea = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('图书简介不能为空！'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          pass: '',
          checkPass: '',
          age: '',
          area:""
        },
        rules: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
           area: [
            { validator: validateArea, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          age: [
            { validator: checkAge, trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
        ...mapMutations({
            add:"add"
        }),
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var obj = {
                id:mock.id(),
                title:this.ruleForm.pass,
                isIptShow:false,
                area:this.ruleForm.area,
                url:mock.dataImage("200x100","图片")
            }
            this.add(obj);
            this.$message({
                type:"success",
                message:"添加成功!"
            })
            this.$refs[formName].resetFields();
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style lang="scss" scoped>
.main{
    width: 800px;
    margin: 0 auto;
}
</style>